<template>
  <label class="dw-radio" @click="$emit('change', label)">
    <span class="dw-radio-input">
      <input type="radio" :checked="value == label" />
    </span>
    <span :class="['dw-radio-text', { checked: value == label }]">
      <slot />
    </span>
  </label>
</template>

<script>
export default {
  name: "dwRadio",
  model: {
    prop: "value",
    event: "change",
  },
  props: ["value", "label", "disabled"],
  components: {},
  data() {
    return {};
  },
  methods: {},
};
</script>

<style lang="less" scoped>
.dw-radio {
  cursor: pointer;
}
.dw-radio-text {
  padding: 0 8px;
  font-size: 14px;
  color: #666;
}
input[type="radio"] {
  border-radius: 100%;
  width: 11.5px;
  height: 11.5px;
}
.checked {
  color: @dw-color-primary;
}
</style>
