<template>
  <div class="nav grid" :style="`grid-template: 1fr /repeat(${list.length},1fr);`">
    <div v-for="({ name }, inx) in list" :key="name" :class="['title', $route.name == name ? 'select' : '']" @click="switchNav(name, inx)">{{ name }}</div>
    <div ref="line" class="line" :style="`width:calc(100%/${list.length})`"></div>
  </div>
</template>

<script>
export default {
  // model: {
  //   prop: "cur",
  //   event: "change",
  // },
  // props: {
  //   list: {
  //     type: Array,
  //     required: true,
  //   },
  //   cur: {
  //     type: String,
  //   },
  // },
  data() {
    return {
      list: [],
    };
  },
  methods: {
    switchNav(name, inx) {
      this.moveTo(inx);
      this.$router.push(`/${name}`);
    },
    moveTo(inx) {
      const line = this.$refs["line"].style;
      line.left = `${(100 / this.list.length) * inx}%`;
      //   console.log(line);
    },
  },
  created() {
    // 此处简单处理下路由
    this.list = this.$router.options.routes[0].children;
    console.log("nav列表：", this.$router.options.routes[0].children);
  },
  mounted() {
    const inx = this.list.findIndex(({ name }) => name == this.$route.name);
    // console.log(this.$route.name, inx);
    const line = this.$refs["line"].style;
    line.left = `${(100 / this.list.length) * inx}%`;
  },
};
</script>

<style scoped>
.nav {
  width: 100%;
  height: 50px;
  /* margin-bottom: 30px; */
  position: relative;
}
.title {
  color: #bdc3c7;
  font-size: 1.5em;
  height: 100%;
  /* width: 100%; */
  text-align: center;
  line-height: 2em;
  cursor: pointer;
  user-select: none;
}
.title:not(.select):hover {
  color: #66b1ff;
}
.select {
  color: #3a8ee6;
}
.line {
  /* width: 100%; */
  height: 2px;
  background: #3a8ee6;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: left 0.2s ease-in-out 0s;
}
</style>
